import * as Yup from 'yup';

export const UserInfoSchema = Yup.object().shape({
  name: Yup.string()
    .required(' '),
  username: Yup.string()
    .required(' '),
  email: Yup.string()
    .email('  email')
    .required(' email'),
  city: Yup.string()
    .required(' '),
  phone: Yup.string()
    .required(' '),
  company: Yup.string()
    .required(' '),
});
